<template>
	<div id="container4">
		<div class="div_b">
<!-- 			<el-input v-model="search" placeholder=""></el-input> -->
			<el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" border style="width: 100%">
				<el-table-column label="" prop="OBJECTID">
				</el-table-column>
				<el-table-column label="" prop="TB_ID">
				</el-table-column>
<!-- 				<el-table-column label="" prop="BHMC">
				</el-table-column> -->
				<el-table-column label="" prop="BHLX">
				</el-table-column>
				<el-table-column label="(m)" prop="Area">
				</el-table-column>
				<el-table-column label="" prop="Change_Per">
				</el-table-column>
				<el-table-column label="" prop="Level">
				</el-table-column>
				<el-table-column label="" prop="ZN">
				</el-table-column>
				<el-table-column label="" prop="CM">
				</el-table-column>
				<el-table-column align="right">
<!-- 					<template slot="header">
						<el-input v-model="search" size="mini" placeholder="" />
					</template> -->
					<template>
						<el-button size="mini" @click="exportWord"></el-button>
					</template>
				</el-table-column>
			</el-table>

			<div class="block" style="margin-top:15px;">
				<el-pagination align='center' @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
				:page-sizes="[1,5,8]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="tableData.length"></el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import docxtemplater from 'docxtemplater'
	import PizZip from 'pizzip'
	import JSZipUtils from 'jszip-utils'
	import {
		saveAs
	} from 'file-saver'
	export default {
		data() {
			return {
				tableData: [],
				search: '',
				currentPage: 1, // 
				total: 20, // 
				pageSize: 8 // 

			}
		},
		mounted() {
			var Data = require("../../assets/localdata/json/BH2021.json")
			var that = this
			Data.features.forEach(function(v) {
				console.log(v);
				that.tableData.push(v.attributes)
			});
		},
		methods: {
			handleEdit(index, row) {
				console.log(index, row);
			},
			handleSizeChange(val) {
				console.log(` ${val} `);
				this.currentPage = 1;
				this.pageSize = val;
			},
			// 
			handleCurrentChange(val) {
				console.log(`: ${val}`);
				this.currentPage = val;
			},
			// word
			exportWord: function() {
				let that = this;
				// 
				JSZipUtils.getBinaryContent("/ - .docx", function(error, content) {
					// model.docx
					// 
					if (error) {
						throw error;
					}

					// PizZip
					let zip = new PizZip(content);
					// docxtemplater
					let doc = new docxtemplater().loadZip(zip);
					// 
					doc.setData({
						table: that.tableData[29] //word
					});

					try {
						// 
						doc.render();
					} catch (error) {
						// 
						let e = {
							message: error.message,
							name: error.name,
							stack: error.stack,
							properties: error.properties
						};
						console.log(JSON.stringify({
							error: e
						}));
						throw error;
					}

					// docxtemplaterzip
					let out = doc.getZip().generate({
						type: "blob",
						mimeType: "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
					});
					// 
					saveAs(out, ".docx");
				});
			}
		},
	}
</script>

